import React, { useEffect } from "react";
import { Tooltip } from "carbon-components-react";
import "./similarityview.css";
export default function SimilarityView(props) {
  //TODO do we want to do something with incorrect results? e.g. visualize differently?
  useEffect(() => {
    // Update the document title using the browser API
    // document.title = `You clicked ${count} times`;
  });

  const selections = props.selections;

  const imageBasePath =
    selections.basePath +
    "/assets/semsearch/datasets/" +
    selections.dataset.name +
    "/";
  const selectedImagePath = imageBasePath + props.selectedImage + ".jpg";
  const selectedCat = selections.dictionary[props.selectedImage];

  const topSimilar = selections.topSimilar;
  let simCount = 0;
  let modelScore = 0;
  let totalScore = 0;
  let incorrectResults = [];

  for (var i in props.similarityArray.slice(1, topSimilar + 1)) {
    if (
      String(selectedCat) ===
      String(selections.dictionary[props.similarityArray[i]])
    ) {
      simCount++;
      modelScore += (topSimilar - i) / topSimilar;
    } else {
      incorrectResults.push(props.similarityArray[i]);
    }
    totalScore += (topSimilar - i) / topSimilar;
  }

  // console.log(selectedImagePath);

  const similarImagesList = props.similarityArray.map((alldata, index) => {
    let imagePath = imageBasePath + alldata + ".jpg";
    // console.log(imagePath)

    let correctCategory =
      selections.dictionary[props.selectedImage] ===
      selections.dictionary[alldata];
    return (
      <div
        key={alldata + "winper"}
        className="iblock similarityfullbox mr5 mb5 positionrelative"
      >
        <div
          style={{ backgroundColor: correctCategory ? "green" : "red" }}
          className="trigreen"
        ></div>

        <img
          key={alldata + "image" + alldata}
          // onMouseOver={this.hoverSimilarImage.bind(this)}
          onClick={() => props.setSelectedImage(alldata)}
          src={imagePath}
          alt=""
          className={"simiimage simcomp clickable rad2 "}
          indexvalue={alldata}
        />
      </div>
    );
  });

  const searchPerformanceBox = (
    <div className="floatleft iblock mr10 rad3 ">
      {/* <div className="pb5 smalldesc "> Search result score </div> */}
      <div className="topscorediv">
        <div className="mainscore  topmainscore">
          {((modelScore / totalScore) * 100).toFixed(1) + "%"}{" "}
        </div>

        <div className="weightedscore smalldesc textaligncenter">
          <Tooltip direction={"right"} triggerText="What is this?">
            <div className="wscore">
              This is the percentage of returned results that belong to the same
              category as the selected image (weighted by position in the result
              list). For the current search,{" "}
              <strong>
                {simCount} / {topSimilar} results{" "}
              </strong>{" "}
              are in same category{" "}
              <strong>({selectedCat.toUpperCase()})</strong>. Note that this
              score is conservative - some images may belong to different
              classes but are <span className="italics"> similar </span> (e.g
              sedan, beetle, ferrari are <span className="italics">all</span>{" "}
              cars).
            </div>
          </Tooltip>
        </div>
      </div>
    </div>
  );

  return (
    <div
      style={{
        display: selections.show.getter.similarDrawer ? "" : "none",
      }}
      className=" mb10"
    >
      <div className="sliderboxcontainer transition3s">
        <div className={" sliderbox topconfig" + (true ? " open" : " closed")}>
          <div className="glowbar transitionw6s mb7 w0"></div>
          <div className="flex ">
            <div className="iblock h100 positionrelative  mr10">
              <img
                src={selectedImagePath}
                className="mainsimilarityimage rad4  iblock"
                alt=""
              />
              <div className="mt5  mainsimilaritytitle  p10  lightbluehightlight">
                <div className="boldtext iblock mediumdesc mr5">
                  {" "}
                  SELECTED IMAGE{" "}
                </div>
                <div className="iblock smalldesc pt5 ">
                  {" "}
                  {selectedCat.toUpperCase()} <strong> </strong>
                </div>
              </div>

              {/* <div> searchimi number {this.state.selectedsimimage}</div> */}
            </div>
            <div className=" flexfull">
              <div className="scrollwindow simiwindow ">
                {searchPerformanceBox}
                {similarImagesList.slice(1, topSimilar)}
                <br />
                <br />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
